@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;800&display=swap);
.home{
    position: relative;
    width: 100%;
    min-height: calc(100% - 235px);
}

.align-center-container{
    position: absolute;
    width: 100%;
    padding: 25px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    overflow: hidden;
}

.home-flex-container{
    display: flex;
    justify-content: center;
}

.line-1{
    position: relative;
    color: var(--primary-color);
    font-size: 2.5rem;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    white-space: pre-wrap;
    overflow: hidden;
    margin: 0px 3px;
}

.about-me{
    margin:calc(3*var(--margin)) auto;
    max-width: 700px;
    font-size: 1.2rem;
    color: var(--black-shade);
    -webkit-animation: visible linear 1s;
            animation: visible linear 1s;
    text-align: center;
}

/* Animation */
.anim-typewriter{
    border-right: 2px solid var(--primary-color);
    -webkit-animation: typewriter 2s steps(11) 1s 1 normal both,
               blinkTextCursor 500ms steps(11) infinite normal;
            animation: typewriter 2s steps(11) 1s 1 normal both,
               blinkTextCursor 500ms steps(11) infinite normal;
  }
  @-webkit-keyframes typewriter{
    from{width: 0;}
    to{width: 18.5rem;}
  }
  @keyframes typewriter{
    from{width: 0;}
    to{width: 18.5rem;}
  }
  @-webkit-keyframes blinkTextCursor{
    from{border-right-color: var(--primary-color);}
    to{border-right-color: transparent;}
  }
  @keyframes blinkTextCursor{
    from{border-right-color: var(--primary-color);}
    to{border-right-color: transparent;}
  }
  @-webkit-keyframes visible{
      from{
          -webkit-transform: translateY(25px);
                  transform: translateY(25px);
          opacity: 0;
      }
      to{
          -webkit-transform: translateY(0px);
                  transform: translateY(0px);
          opacity: 1;
      }
  }
  @keyframes visible{
      from{
          -webkit-transform: translateY(25px);
                  transform: translateY(25px);
          opacity: 0;
      }
      to{
          -webkit-transform: translateY(0px);
                  transform: translateY(0px);
          opacity: 1;
      }
  }

  @media only screen and (max-width: 500px){
    .home-flex-container{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
  }
.projects{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.flex-container{
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
}

.project-link{
    color: var(--black);
}

.project-card{
    padding: calc(2*var(--padding));
    border: 1px solid var(--white-shade-two);
    border-radius: var(--border-radius);
    max-width: 310px;
    min-height: 180px;
    max-height: 180px;
    transition: -webkit-transform linear 0.3s;
    transition: transform linear 0.3s;
    transition: transform linear 0.3s, -webkit-transform linear 0.3s;
    margin: 10px;
}

.project-title{
    margin-bottom: var(--margin);
}

.project-description{
    margin-bottom: var(--margin);
}

/* Hover */
.project-card:hover {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    box-shadow: 0 14px 28px rgba(0,0,0,0.07), 0 10px 10px rgba(0,0,0,0.04);
}
.about{
    width: 100%;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
}

.about-flex-wraper{
    width: 70%;
    margin: 0 auto;
}

.about-title{
    margin-bottom: var(--margin);
}

.about-sub-title{
    margin-bottom: var(--margin);
}

.about-description{
    margin-bottom: var(--margin);
}

.skills{
    display: flex;
    flex-wrap: wrap;
}

.skill-set{
    margin-right: calc(6*var(--margin));
    margin-top: calc(4*var(--margin));
    margin-bottom: calc(4*var(--margin));
}

.skill-heading{
    font-weight: 400;
    margin-bottom: var(--margin);
}

.skill{
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 250px;
    padding: var(--padding);
}

.skill:after{
    position: absolute;
    content: "";
    display: block;
    bottom: 0px;
    height: 0.30em;
    border-radius: 3px;
    -webkit-animation: grow linear 0.5s;
            animation: grow linear 0.5s;
}

.skill-one::after{
    background-color: var(--success);
    width: 80%;
}

.skill-two::after{
    background-color: var(--cyan);
    width: 70%;
}

.skill-three::after{
    background-color: var(--primary-color);
    width: 70%;
}

.skill-four::after{
    background-color: var(--pink);
    width: 60%;
}

.skill-five::after{
    background-color: var(--danger);
    width: 50%;
}

.skill-six::after{
    background-color: var(--cyan);
    width: 80%;
}

.skill-seven::after{
    background-color: var(--success);
    width: 70%;
}

.skill-eight::after{
    background-color: var(--primary-color);
    width: 65%;
}

/* Animation */

@-webkit-keyframes grow{
    from{
        width: 0%;
    }
}

@keyframes grow{
    from{
        width: 0%;
    }
}
.contact-me{
    position: relative;
    width: 100%;
    height: calc(100% - 235px);
    display: flex;
    justify-content: center;
    align-items: center;
}

#contact-me-form{
    width: 80%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
}

#my-form-status{
    color: var(--primary-color);
}

.text-area{
    -webkit-appearance: none;
    outline: none;
    padding: var(--padding);
    border: 1px solid var(--white-shade-two);
    border-radius: calc(var(--border-radius)/2);
    font-family: 'Poppins', sans-serif;
    width: 100%;
    max-width: 500px;
    margin: var(--margin) 0;
}

.submit-button{
    -webkit-appearance: none;
    outline: none;
    border: none;
    cursor: pointer;
    background-color: var(--primary-color);
    color: var(--white);
    width: 50px;
    border-radius: calc(var(--border-radius)/2);
    padding: var(--padding);    
    margin: var(--margin) 0;

}

/* Focus */
.text-area:focus{
    border: 1px solid var(--primary-color);
}
.app{
    width: 100%;
    height: 100%;
}

.app-header{
    width: 100%;
    max-width: 900px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding: 50px 0;
    font-family: 'Poppins', sans-serif;
}

.app-header p{
    font-size: 1.2rem;
    font-weight: 500;
}

.nav-bar ul{
    display: flex;
}

.line-2{
    cursor: pointer;
    padding: var(--padding);
}

.line-2:after{
    content: "";
    display: block;
    height: 0.15em;
    background: var(--primary-color);
    width: 0px;
    transition: width 0.35s;
}

.app-footer{
    margin: 0 auto;
    width: 300px;
    padding: calc(var(--padding)/2);
    text-align: center;
}

.social-media-links ul{
    display: flex;
    justify-content: space-around;
    margin:0px calc(4*var(--margin));
}

.social-media-links ul li a{
    color: var(--black-shade);
    transition: all ease 200ms;
}

/* Hover */
.social-media-links ul li a:hover{
    color: var(--black);
}

.line-2:hover:after{
    width: 100%;
}

/* Media */
@media(max-width: 500px){
    .app-header{
        flex-direction: column;
        align-items: center;
    }
}
*, *:before, *:after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

:root{
  --primary-color: #8A2BE2;
  --assent-color: #007bff;
  --white: #FFFFFF;
  --black: #000000;
  --white-shade-one: #EFEEF5;
  --white-shade-two: #E2E1E0;
  --white-shate-three: #fafafb;
  --black-shade: #727272;
  --success: #28a745;
  --danger: #dc3545;
  --cyan: #17a2b8;
  --pink: #e83e8c;
  --padding: 10px; /*Common padding value for all components*/
  --margin: 10px; /*Common margin value for all components*/
  --border-radius: 10px; /*Common border radius value for all components*/
}

li{
  list-style: none;
}

a{
  text-decoration: none;
}

body {
  position: relative;
  width: 100vw;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  width: 100%;
  height: 100%;
}

